@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* temporary component */
.titletext {
  font-size: 30px;
  color: white;
  padding: 10px;
  text-align: center;
}

body {
  background-color: #1F2124;
  overflow-x: hidden;
}
* {
  font-family: 'NanumSquare';
  font-weight: 700;
}

.main-icon {
  -webkit-align-items: center;
          align-items: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  float: left;
}

.main-img {
  padding: 10px 20px;
  height: 40px;
  border-radius: 50%;
}

@media screen and (max-width: 600px) {
  .nav .text-link:not(.active) {
    display: none;
  }
  .nav .icon {
    float: right;
    display: block;
    padding: 20px;
    position: absolute;
    right: 0;
    top: 0;
  }

}


.nav {
  background-color: #2f3136;
  display: -webkit-flex;
  display: flex;

  -webkit-justify-content: space-between;

          justify-content: space-between;

  width: 100%;
  height: 65px;

  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
}

.icon {
  display: none;
  color: #fff;
}

.text-link {
  color: #fff;
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  text-decoration: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@media screen and (max-width: 600px) {
  .active {
    background-color: #2f3136;
    display: block;
    float: none;
    width: 100vw;
    padding: 0 0 20px 0;
  }

  .active:not(:first-child) {
    position: relative;
    padding: 20px;
  }
}


.footer {
  text-align: center;
  font-size: 10px;
  color: #fff;
}
